import * as React from "react"
import { graphql } from "gatsby"

import Layout from "../components/layout"
import Seo from "../components/seo"
import ImageBackground from "../images/images_homenowords-05.jpg"
import { makeStyles } from "@material-ui/core"
import FlickerEffect from "../components/flickerEffect"
const useStyles = makeStyles(() => ({
   image: {
      display: 'flex',
      justifyContent: 'center',
      alignItems: 'center',
      '& div': {
         backgroundImage: `url(${ImageBackground})`,
         backgroundSize: '100% auto',
         backgroundRepeat: 'no-repeat',
         backgroundPosition: 'center',
         display: 'flex',
         justifyContent: 'center',
         alignItems: 'center',
         position:'relative',
         "@media (max-width: 767px)": {
            height: '25vh',
            width: '90vw',
         },
         "@media (min-width: 768px) and (max-width: 1023px)": {
            height: '33vh',
            width: '90vw',
         },
         "@media (min-width: 1024px) and (max-width: 1280px)": {
            height: '35vw',
            width: '70vw',
         },
         "@media (min-width: 1281px) and (max-width: 1439px)": {
            height: '50vh',
            width: '50vw',
         },
         "@media (min-width: 1440px)": {
            height: '48vh',
            width: '45vw'
         },
      }
   },
   configArtical: {
      textAlign: 'justify',
      marginBottom: 10
   },
   dynamicContent: {
      position: 'absolute',
      borderTop: '1px solid white',
      borderBottom: '1px solid white',
      textTransform: 'uppercase',
      color: "white",
      padding: '5px 0',
      textAlign: 'center',
      "@media (max-width: 767px)": {
         width: '60vw',
         top: '45%',
      },
      "@media (min-width: 768px) and (max-width: 1023px)": {
         width: '40vw',
         top: '54%',
      },
      "@media (min-width: 1024px) and (max-width: 1280px)": {
         width: '40vw',
         top: '50%',
      },
      "@media (min-width: 1281px) and (max-width: 1439px)": {
         width: '30vw',
         top: '50%',
      },
      "@media (min-width: 1440px)": {
         width: '23vw',
         top: '55%',
      },
   },
   article:{
      '& h1': {
         textAlign:'left',
         margin:'20px 0 16px 0',
         fontSize:'1.7rem',
         fontColor:'black',
         fontWeight:'400'
      }
   }
}));

const listWordHowWeWork = ['RESPONSIBILITY', 'GIVE BACK TO COMMUNITY', 'GROWTH MINDSET', 'EMPATHY', 'AGILITY', 'WORK/LIFE BALANCE',
   'FAMILY', 'LEARNING', 'CREATIVITY', 'RESPECT', 'HONESTY', 'EXCELLENCE', 'COMPASSION'];

const HowWeWork = ({ data, location }) => {
   const classes = useStyles()
   const siteTitle = data.site.siteMetadata?.title || `Title`

   return (
      <Layout location={location} title={siteTitle}>
         <Seo title="How We Work"  description="The company makes good software" />
         <div>
            <div className={classes.image}>
               <div>
                  <p className={classes.dynamicContent}>
                     <FlickerEffect listWord={listWordHowWeWork}></FlickerEffect>
                  </p>
               </div>
            </div>
            <article className={classes.article}>
               <h1>How we work</h1>
               <p className={classes.configArtical}>The Izundo team takes pride in our focus on <b><i>excellence</i></b> both in what we produce and in the way
               we serve our clients. We remain <b><i>agile</i></b> in this ever-changing world and build our systems around achieving <b><i>work/life balance.
               Creativity</i></b> is a must-have strength in order to achieve this goal.</p>

               <p className={classes.configArtical}>We are an international community that carries <b><i>empathy</i></b> and <b><i>compassion</i></b> for our fellow team
               members and client partners and insists on <b><i>honesty</i></b> and <b><i>respect</i></b> as a priority in our interactions.</p>

               <p className={classes.configArtical}>We employ a <b><i>growth mindset</i></b> mentality, appreciating our mistakes and challenges as <b><i>learning</i></b> opportunities 
               so that we become better in all respects.</p>

               <p className={classes.configArtical}>Our <b><i>work ethic</i></b> values our <b><i>family</i></b> , figurative and real, and we take opportunities to <b><i>give back
                to our community</i></b> as one of many important ways to contribute to making the world a better place.</p>

               <p className={classes.configArtical}>And finally, we take <b><i>responsibility</i></b> for what we do and how we do it. The buck stops with each and
                every one of our team members. And each and every one of us dreams big enough to change the world.</p>
            </article>
         </div>
      </Layout>
   )
}

export default HowWeWork

export const pageQuery = graphql`
  query {
    site {
      siteMetadata {
        title
      }
    }
    allMarkdownRemark(sort: { fields: [frontmatter___date], order: DESC }) {
      nodes {
        excerpt
        fields {
          slug
        }
        frontmatter {
          date(formatString: "MMMM DD, YYYY")
          title
          description
        }
      }
    }
  }
`
